<template>
  <section class="section-work">
    <div class="container">
      <div class="work-list">
        <swiper
          :slidesPerView="isMobile ? 1 : 3"
          :spaceBetween="30"
          :pagination="{
            clickable: true,
          }"
          :modules="modules"
          class="mySwiper"
        >
          <swiper-slide
            v-for="work  in works"
            :key="work.id"
          >
            <div class="card-box">
              <div class="inner">
                <h3>{{ work.title }}</h3>
                <span class="date">{{ work.date }}</span>
                <ul>
                  <li>Skill : {{ work.skill }}</li>
                  <li>Role : {{ work.role }}</li>
                </ul>
              </div>
            </div>
          </swiper-slide>
        </swiper>

      </div>
    </div>  
  </section>
</template>

<script>
  // axios
  import axios from 'axios';
  //mediaQuery
  import { useMediaQuery } from "@vueuse/core";
  // Import Swiper Vue.js components
  import { Swiper, SwiperSlide } from 'swiper/vue';
  // Import Swiper styles
  import 'swiper/css';
  import 'swiper/css/pagination';
  // import required modules
  import { Pagination } from 'swiper/modules';

  export default {
    components: {
      Swiper,
      SwiperSlide,
    },
    
    setup() {
      const isMobile = useMediaQuery("(max-width: 780px)");

      return {
        isMobile,
        modules: [Pagination]
      };
    },

    data() {
      return {
        // work 데이터
        works : []
      }
    },

    async created() {
      await this.getWorksData();
    },

    methods : {
      async getWorksData() {
        try {
          const response = await axios.get('https://joohee-lee.github.io/vue/data/works.json'); // works.json 파일 경로 설정
          this.works = response.data; //응답 데이터를 works 배열에 저장

        }catch(error) {
          console.error('데이터 로드 중 오류 발생:', error);
        }
      }
    }
  };
</script>

