    color: $color-point-primary;
    text-shadow: -5px 10px 30px $color-point-second;
  }

  .date {
    display: inline-block;
    margin-top: 10px;
    font-size: 14px;
    font-weight: 500;
    padding: 4px 10px;
    background: $color-point-primary;
    border-radius: 4px;
    color: #fff;
  }

  ul {
    position: absolute;
    left: 20px;
    right: 20px;
    top: calc(  100% * 2/3);
    transform: translateY(-20%);
    padding: 0 10px;
    li {
      position: relative;
      color: #000;
      font-weight: 300;
      font-size: 16px;
      line-height: 1.5;
      word-wrap: break-word;
      padding-left: 10px;

      &:before {
        content: '-';
        display: inline-block;
        position: absolute;
        left: 0;
        top: 0;
      }
    }
  }

  @media (max-width: 780px) {
    h3 {
      font-size: 18px;
      font-weight: 100;
