<script>
import axios from 'axios'

export default {

  data() {
    return {
      notes: [] // JSON 데이터를 저장할 배열
    }
  },
  
  async created() {
    await this.getNotesData(); // 컴포넌트가 생성되면 JSON 데이터 로드
  },

  methods: {
    async getNotesData() {
      try {
        const response = await axios.get('https://joohee-lee.github.io/vue/data/notes.json'); // arts.json 파일 경로 설정
        this.notes = response.data; //응답 데이터를 arts 배열에 저장

        console.log("test : " + this.arts);
      }catch(error) {
        console.error('데이터 로드 중 오류 발생:', error);
      }
    }
  }
}
</script>

