<template>
  <div 
    v-for="art in arts"
    :key="art.id"
    class="slide">
    <div class="slide-inner">
      <div class="img">
        <img :src="art.url" alt=""/>
      </div>
      <div class="text">
        <dl>
          <dt>{{ art.title }}</dt>
          <dd>{{ art.genre }}</dd>
          <dd>{{ art.size }}</dd>
          <dd>{{ art.date }}</dd>
        </dl>
      </div>
     </div>
  </div>
</template>

<script>
  import axios from 'axios'

  export default {

    data() {
      return {
        arts: [] // JSON 데이터를 저장할 배열
      }
    },
    
    async created() {
      await this.fetchArtsData(); // 컴포넌트가 생성되면 JSON 데이터 로드
    },
  
    methods: {
      async fetchArtsData() {
        try {
          const response = await axios.get('https://joohee-lee.github.io/vue/data/arts.json'); // arts.json 파일 경로 설정
          this.arts = response.data; //응답 데이터를 arts 배열에 저장

          console.log("test : " + this.arts);
        }catch(error) {
          console.error('데이터 로드 중 오류 발생:', error);
          this.arts = [
            {
              url: "https://joohee-lee.github.io/vue/art/art-01.jpg",
              name: "Leejoohee",
              title: "Night Rose",
              genre: "oil on canvas",
              size: "Cotton canvas / F20 /72.7*60.6",
              date: "2024/10"
            }
          ];
        }
      }
    }
  }
</script>

